/* src/App.css */
.export-buttons {
  margin-top: 20px;
}

.export-buttons button {
  margin-right: 10px;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
}

.export-buttons button:last-child {
  background-color: #28a745;
}

.export-buttons button:hover {
  opacity: 0.8;
}